import { getSongListComments, getAlbumComments } from '@/request/api/songListDetail';
import { getMusicComment } from '@/request/api/songInfo';
import { getCommentMv } from '@/request/api/videoPlayer';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 歌单 id
      id: 0,
      // 类型(类型 1 为歌单的更多精彩评论，2 为专辑，3 为歌曲, 4 为 Mv)
      type: 0,
      // 所有精彩评论(最多返回 15条？)
      hotComments: []
    };
  },

  async mounted() {
    // console.log('精彩评论页接收到歌单/歌曲 id：' + this.$route.query.id);
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    this.id = this.$route.query.id;
    this.type = this.$route.query.type; // console.log(this.id, this.type);
  },

  methods: {
    // 根据 type申请不同的请求
    async getData() {
      this.isLoading = 1;

      if (this.type == 1) {
        // 获取歌单的精彩评论(最多返回 15条)
        const res = await getSongListComments(this.id, sessionStorage.getItem("cookie")); // console.log('歌单精彩评论', res)

        this.hotComments = res.data.hotComments;
      } else if (this.type == 2) {
        // 获取专辑的(最多返回 15条)
        const res = await getAlbumComments(this.id, sessionStorage.getItem("cookie")); // console.log('专辑精彩评论', res)

        this.hotComments = res.data.hotComments;
      } else if (this.type == 3) {
        // 获取歌曲的(最多返回 15条)
        const res = await getMusicComment(this.id, 100, sessionStorage.getItem("cookie")); // console.log('歌曲精彩评论', res)

        this.hotComments = res.data.hotComments;
      } else if (this.type == 4) {
        // 获取 MV 的(最多返回 15条)
        const res = await getCommentMv(this.id, sessionStorage.getItem("cookie")); // console.log('MV 精彩评论', res)

        this.hotComments = res.data.hotComments;
      } else {
        return;
      }

      this.isLoading = 0; // console.log('所有精彩评论', this.hotComments);
    },

    // 评论发布时间的处理(默认精彩评论都不是昨天发布的)
    publishTime(time) {
      // 获取当前年份
      let YNow = new Date().getFullYear(); // 把返回的时间戳转为时间格式

      let date = new Date(time);
      let Y = date.getFullYear();
      let M = date.getMonth() + 1;
      let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      let H = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let Min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes(); // 今年发布的评论不需显示年份

      if (Y === YNow) return M + '月' + D + '日 ' + H + Min;else return Y + '年' + M + '月' + D + '日 ' + H + Min;
    },

    // 鼠标位于某个评论处
    mouseover(index) {
      for (let i = 0; i < this.$refs.report.length; i++) {
        this.$refs.report[i].style.display = 'none';
      }

      this.$refs.report[index].style.display = 'block';
    },

    // 鼠标移出所有评论
    mouseleave() {
      for (let i = 0; i < this.$refs.report.length; i++) {
        this.$refs.report[i].style.display = 'none';
      }
    },

    // 鼠标移入某个点赞
    zanMouseenter(index) {
      // this.$refs.zanIcon 与 this.$refs.zanNum 数组长度一致
      for (let i = 0; i < this.$refs.zanIcon.length; i++) {
        this.$refs.zanIcon[i].style.fill = '#545454';
        this.$refs.zanNum[i].style.color = '#a7a5a5';
      }

      this.$refs.zanIcon[index].style.fill = '#000';
      this.$refs.zanNum[index].style.color = '#000';
    },

    // 鼠标移出所有点赞
    zanMouseleave() {
      for (let i = 0; i < this.$refs.zanIcon.length; i++) {
        this.$refs.zanIcon[i].style.fill = '#545454';
        this.$refs.zanNum[i].style.color = '#a7a5a5';
      }
    },

    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    }

  },
  watch: {
    type() {
      // console.log('type 改变了！');
      // 根据 type申请不同的请求
      this.getData();
    },

    // 监听路由参数 $route，路由参数改变则重新请求数据 和页面重新渲染
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // console.log('newRoute:', newRoute.query.id, 'oldRoute:', oldRoute.query.id);
      // console.log('newRoute:', newRoute.query.type, 'oldRoute:', oldRoute.query.type);
      this.id = newRoute.query.id;
      this.type = newRoute.query.type; // 根据 type申请不同的请求

      this.getData();
    }
  }
};