// 导入 axios 实例
import service from "..";

// 获取 mv 数据
export function getMvDetail(mvid, cookie) {
  return service({
    method: 'GET',
    url: `/mv/detail?mvid=${mvid}&cookie=${cookie}`
  })
}
// 获取 mv 点赞转发评论数数据
export function getMvDetailInfo(mvid, cookie) {
  return service({
    method: 'GET',
    url: `/mv/detail/info?mvid=${mvid}&cookie=${cookie}`
  })
}
// 获取 mv 评论
export function getCommentMv(mvid, cookie) {
  return service({
    method: 'GET',
    url: `/comment/mv?id=${mvid}&cookie=${cookie}`
  })
}
// 获取 mv 播放地址
export function getMvAdd(mvid, cookie) {
  return service({
    method: 'GET',
    url: `/mv/url?id=${mvid}&cookie=${cookie}`
  })
}
