// 导入 axios 实例
import service from "..";

// 获取歌单详情(需登录)
export function getSongListDetail(id, cookie) {
  return service({
    method: 'GET',
    url: `/playlist/detail?id=${id}&cookie=${cookie}`
  })
}

// 获取歌单所有歌曲(需登录)
export function getSongListSongs(id, cookie) {
  return service({
    method: 'GET',
    url: `/playlist/track/all?id=${id}&cookie=${cookie}`
  })
}

// 获取歌单评论(需登录)
export function getSongListComments(id, cookie) {
  return service({
    method: 'GET',
    url: `/comment/playlist?id=${id}&cookie=${cookie}`
  })
}

// 获取歌单的所有收藏者(需登录)
export function getSongListSubscribers(id, cookie, limit) {
  return service({
    method: 'GET',
    url: `/playlist/subscribers?id=${id}&cookie=${cookie}&limit=${limit}`
  })
}

// 获取专辑内容(需登录)
export function getAlbumDetail(id, cookie) {
  return service({
    method: 'GET',
    url: `/album?id=${id}&cookie=${cookie}`
  })
}

// 获取专辑动态信息(可获得专辑动态信息,如是否收藏,收藏数,评论数,分享数)
export function getAlbumDetailDynamic(id) {
  return service({
    method: 'GET',
    url: `/album/detail/dynamic?id=${id}`
  })
}

// 获取专辑所有歌曲(需登录)
export function getAlbumSongs(id, cookie) {
  return service({
    method: 'GET',
    url: `/album/track/all?id=${id}&cookie=${cookie}`
  })
}

// 获取专辑评论(需登录)
export function getAlbumComments(id, cookie) {
  return service({
    method: 'GET',
    url: `/comment/album?id=${id}&cookie=${cookie}`
  })
}